
.footer {
    margin-top: auto;
    background-color: var(--dark-bg);
    padding: 60px 0 50px;
    color: var(--header-text);
  }
  
  .footer__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 27px;
  }
  
  .social {
    display: flex;
    column-gap: 30px;
    align-items: center;
  }
  .social__item {
  }
  
  .copyright {
    font-size: 16px;
  }
  .copyright p + p {
    margin-top: 0.5em;
  }
  
