.nav {
    padding: 20px 0;
    border-bottom: 1px solid var(--dark-border);
    background-color: var(--dark-bg);
    color: var(--header-text);
    letter-spacing: normal;
  }
  
  .nav-row {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    column-gap: 30px;
    row-gap: 20px;
    flex-wrap: wrap;
  }
  
  .logo {
    margin-right: auto;
    color: var(--header-text);
    font-size: 24px;
    font-family: "Poppins", sans-serif;
  }
  
  .logo strong {
    font-weight: 700;
  }
  
  .nav-list {
    display: flex;
    flex-wrap: wrap;
    row-gap: 10px;
    align-items: center;
    column-gap: 40px;
    font-size: 16px;
    font-weight: 500;
    font-family: "Poppons", sans-serif;
  }
  
  
  
  .nav-list__link {
    color: var(--header-text);
    transition: opacity 0.2s ease-in;
  }
  
  .nav-list__link:hover {
    opacity: 0.8;
  }
  
  .nav-list__link--active {
    position: relative;
  }
  
  .nav-list__link--active::before {
    content: "";
  
    position: absolute;
    left: 0;
    top: 100%;
  
    display: block;
    height: 2px;
    width: 100%;
    background-color: var(--accent);
  }

  .nav-row {
    justify-content: space-between;
  }

  .dark-mode-btn {
    order: 0;
  }