.header {
    padding: 40px 0;
    min-height: 695px;
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    background-color: var(--black);
    background-image: url("./../../img/Abstract.png");
    background-repeat: no-repeat;
    background-size: auto;
    background-position: center center;
  
    color: var(--header-text);
    text-align: center;
  }
  
  .header__wrapper {
    padding: 0 15px;
    max-width: 660px;
  }
  .header__title strong {
    font-size: 60px;
    font-weight: 700;
  }
  
  .header__title em {
    font-style: normal;
    color: var(--accent);
  }
  
  .header__title {
    margin-bottom: 20px;
    font-size: 40px;
    font-weight: 500;
    line-height: 1.4;
  }
  
  .header__text {
    margin-bottom: 40px;
    font-size: 18px;
    line-height: 1.333;
  }
  
  .header__text p + p {
    margin-top: 0.5em;
  }

  
  @media (max-width: 620px) {
    /*HEADER*/
    .header {
      min-height: unset;
    }
    .header__title {
      font-size: 30px;
    }
    .header__title strong {
      font-size: 40px;
    }
  }